.sidedrawer {
  width: 70%;
  background: #fff;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  transform: translateX(-100%);
  transition: all 0.3s ease-out;
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  overflow-y: auto;
}
.line {
  border-top: 1px solid #eee;
}

.nav_title {
  margin-top: 20px;
  font-size: 20px;
  text-transform: uppercase;
  color: #111;
}
.nav_title_after {
  border-top: 3px solid #eee;
  width: 60px;
  display: block;
  margin-top: 14px;
  margin-bottom: 10px;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.sidedrawer.show {
  transform: translateX(0);
}

.sidedrawer__links {
  display: flex;
  
  flex-direction: column;
  list-style: none;
}

.sidedrawer__links > li {
  display: flex;
  align-items: center;
}

.sidedrawer__links > li > a:hover {
  /* background: #333; */
  /* color: #f4f4f4; */
}

.text_link{
  margin-top: 10px;
  height: 30px;
  text-decoration: none;
  color: #171717;
  font-size: 1.0rem;
}

.sidedrawer__links > li > a {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  text-align: center;
  flex: 1;
  /* padding: 5px; */
  margin-top: 10px;
  height: 30px;
  text-decoration: none;
  color: #171717;
  font-size: 1.0rem;
}

.sidedrawer__links > li > p {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex: 1;
  padding: 1rem;
  text-decoration: none;
  color: #171717;
  font-size: 1.6rem;
}

.sidedrawer__links > li > a > span {
  display: flex;
  align-items: center;
}

.sidedrawer__cartbadge {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background-color: #171717;
  border-radius: 50%;
  margin-left: 8px;
  color: #fff;
  font-size: 1rem;
}

.sidedrawer__links > li > a:hover .sidedrawer__cartbadge {
  background-color: #fff;
  color: #171717;
}

@media (min-width: 960px) {
  .sidedrawer {
    display: none;
  }
}
