.navbar {
    max-width: 1300px;
    margin: auto;
    /* background-color: #000; */
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 1.5rem 1rem;
    z-index: 50;
  }
  
  .facebook {
    color: #ffffff;
  }
  
  .title {
    width: 100%;
    /* align-items: left; */
    /* text-align: left; */
  
  }
  .bar_container {
    background-color: #000;
  }
  
  .bar {
    max-width: 1300px;
    margin: auto;
    width: 100%;
    height: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    /* z-index: 50; */
  }
  
  
  .logo {
    border-radius: 70%;
    height: 100px;
    width: 100px;
  }
  
  .navbar__logo h2 {
    color: #f4f4f4;
    font-size: 1.4rem;
    cursor: pointer;
  }
  
  .navbar__links {
    display: flex;
    list-style: none;
    align-items: center;
  }
  
  .navbar__links > li {
    padding-left: 1.5rem;
  }
  
  .navbar__links > li > a {
    text-decoration: none;
    color: #f4f4f4;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
  }
  .navbar__links > li > p {
    text-decoration: none;
    color: #f4f4f4;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .navbar__links > li > a > span {
    display: flex;
    align-items: center;
    margin-left: 8px;
  }
  
  .cart__link {
    background: #333;
    padding: 10px;
    border-radius: 8px;
  }
  
  .cart__link:hover {
    background: #dd219e;
    color: #f4f4f4;
  }
  
  .cartlogo__badge {
    width: 30px;
    height: 30px;
    background-color: #f4f4f4;
    border-radius: 50%;
    margin-left: 8px;
    color: #171717;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }
  
  .hamburger__menu {
    display: flex;
    width: 25px;
    height: 18px;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }
  
  .hamburger__menu:hover > div {
    background: #dd219e;
  }
  
  .hamburger__menu > div {
    width: 100%;
    height: 3px;
    background: #000;
    
  }
  
  @media (max-width: 960px) {
    .navbar__links {
      display: none;
    }
  
    .hamburger__menu {
      display: flex;
    }
  }
  
  @media (max-width: 500px) {
    .navbar__logo h2 {
      font-size: 1rem;
    }
  }
  
  
  .socials_bar{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin: 1rem 0 3rem 0; */
  }
